import robbyTags from '@/components/robby-tags/robby-tags.vue'
export default {
	data() {
		return {
			modalName: null,
			imgList: [],
			// 创建标签
			// 删除图标是否可见
			enableDel: true,
			// 添加图标是否可见
			enableAdd: true,
			// 颜色
			colorType: 'primary',
			tagList: ['建筑', '动漫', '艺术'],
			// 单选框
			radio: ''
		}
	},
	components: {
		robbyTags
	},
	methods: {
		RadioChange(e) {
			this.radio = e.detail.value
		},
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		DelImg(e) {
			uni.showModal({
				content: '确定要删除这段回忆吗？',
				cancelText: '留下',
				confirmText: '删除',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		addTag(e) {
			console.log(e)
		},
		delTag(e) {
			console.log(e)
		}
	}
}
